import axios from "axios";
import React, { Component } from "react";
import DisplayCard from "./DisplayCard";
import "./Card.css";

class Card extends Component {
  constructor(props) {
    super(props);
    this.state = {
      deck: null,
      drawn: [],
    };
    this.handelClick = this.handelClick.bind(this);
  }
  async componentDidMount() {
    let deck = await axios.get(
      "https://deckofcardsapi.com/api/deck/new/shuffle"
    );
    this.setState({ deck: deck.data });
  }

  async handelClick() {
    try {
      let cardRes = await axios.get(
        `https://deckofcardsapi.com/api/deck/${this.state.deck.deck_id}/draw/`
      );
      if (!cardRes.data.success) {
        throw new Error("NO CARD REMAINING");
      }
      let card = cardRes.data.cards[0];
      this.setState((st) => ({
        drawn: [
          ...st.drawn,
          {
            id: card.code,
            image: card.image,
            name: `${card.value} of ${card.suit} `,
          },
        ],
      }));
    } catch (err) {
      alert(err);
    }
  }

  render() {
    const cards = this.state.drawn.map((c) => (
      <DisplayCard name={c.name} image={c.image} />
    ));
    return (
      <div className="main">
        <h1 className="title">Card Dealer</h1>

        <button className="btn" onClick={this.handelClick}>
          Click Me
        </button>
        <div className="Deck">{cards}</div>
      </div>
    );
  }
}

export default Card;
